import z from "zod";

export const emailContactSchema = z.object({
  name: z.string().min(1, "El nombre es obligatorio"),
  email: z.string().min(1, "El correo es obligatorio").email("Correo invlido"),
  phone: z.string().min(1, "El telfono es obligatorio").regex(/^\d+$/, "Telfono solo debe contener numeros").min(8, "El telfono es muy corto").max(10, "El telfono es muy largo"),
  message: z.string().min(1, "El mensaje es obligatorio").min(10, "El mensaje debe tener al menos 10 caracteres")
});

export type emailContactFormValues = z.infer<typeof emailContactSchema>